var subName1="马克思主义基本原理概论"; 
	var subName2="毛泽东思想和中国特色社会主义理论体系概论";
	var subName3="中国近代史纲要"; 
	var subName4="思想道德修养与法律基础";
	var subName5="形势与政策以及当代世界经济与政治"; 
	let listData = [
		{
			    id:-1, 
				name: '添加动画效果',
				data: [{
					type: true,
					subName: subName1,
					showAnimation: true,
					subList: []
				}]
			},
			{
				 id:-1, 
					name: '添加动画效果',
					data: [{
						type: true,
						subName: subName2,
						showAnimation: true,
						subList: []
					}]
				},
				{
					 id:-1, 
						name: '添加动画效果',
						data: [{
							type: true,
							subName: subName3,
							showAnimation: true,
							subList: []
						}]
					},
					{
						 id:-1, 
							name: '添加动画效果',
							data: [{
								type: true,
								subName: subName4,
								showAnimation: true,
								subList: []
							}]
						},
						{
							 id:-1, 
								name: '添加动画效果',
								data: [{
									type: true,
									subName: subName5,
									showAnimation: true,
									subList: []
								}]
							}
		
	]	
export default {
	data() {
		return {
			tabIndex: 0,
			labels: [
				{
					label: "练习"
				},
				{
					label: "试卷"
				}
				
			],
			list: [{data:[]},{data:[]}]
		};
	},
	onLoad(){
		for(var i=0;i<5;++i)
		{
		 this.load(i+1);
		}
		this.loadExam();
		this.list[0].data=listData;
		/* console.log(this.list);
		this.list[0]=[{id:0,title:'政治'},{id:1,title:'马原'}]; */
		//this.list[1]=[{id:0,title:'政治'},{id:1,title:'马原'}];
	},
	methods:{
		load:async function(pid)
		{
			var url="http://192.168.43.202:8081/chapter-question/getChildChapter?pid="+pid;
			
			var[error,res] = await uni.request({
					url:url 
				});
				if(res.data.code==200){
					       /* var data={
					        		name: '添加动画效果',
					        		data: [{
					        			type: true,
					        			subName: subName,
					        			showAnimation: true,
					        			subList: []
					        		}]
					        	}; */
							if(res.data.data!=null){
							var chapters=res.data.data;
							
								//title="list.title" :note="list.note"
							for(var i=0;i<chapters.length;++i)
							{
								var chapter={
									title:chapters[i].name,
									note:"",
									id:chapters[i].id
									
								};
							 listData[pid-1].data[0].subList.push(chapter);
							}
						     
						}else{
							var chapter={
								title:无做过的题,
								note:"",
								id:-1
								
							};
							 listData[pid-1].data[0].subList.push(chapter);
							
						}
					
				}
		},
		
		loadExam:async function()
		{
			var url="http://192.168.43.202:8081/exam/history";
			
			var[error,res] = await uni.request({
					url:url 
				});
				if(res.data.code==200){
					       
							if(res.data.data!=null){
							var exams=res.data.data;
							
								//title="list.title" :note="list.note"
							for(var i=0;i<exams.length;++i)
							{
								var data={
									    id:0,
										name: '添加动画效果',
										data: [{
											type: true,
											subName: "",
											note:"",
											showAnimation: true,
											subList: []
										}]
									}; 
								data.id=exams[i].id,
								
								data.data[0].subName=exams[i].name;
								data.data[0].note=exams[i].createAt;
								console.log(data);
								this.list[1].data.push(data);
								
							 
							}
						     
						}else{
							var data={
								    id:0,
									name: '添加动画效果',
									data: [{
										type: true,
										subName: "无数据",
										showAnimation: true,
										subList: []
									}]
								}; 
							data.id=exams[i].id,
							data.data[0].subName=exams[i].name;
							console.log(data);
							this.list[1].data.push(data);
							
							
						}
						/* console.log(data);
						
						listData.push(data); */
						
						
						
				}
		},
		change(e) {
			if(e>0)
			{
				uni.navigateTo({
					url:'/pages/exam/answers?eid='+e
				});
			}
		},
		exam(i){
			if(i>0)
			{
				uni.navigateTo({
					url:'/pages/exam/answers?eid='+i
				});
			}
			
		},
		question(i){
			//var url="http://192.168.43.202:8081/chapter-question/getChildChapter?pid="+pid;
			 uni.request({
				url: "http://192.168.43.202:8081/chapter-question/getQuestions?pid="+i,
				success(res){
					if(res.data.code==200)
					{
						if(res.data.data!=null && res.data.data.length>0)
						{
							console.log(res.data.data);
							uni.navigateTo({
								url:'/pages/user/questionHistory?pid='+i
							});
						}
					}
				}
				
				
			});
			
			
			
		}
	}
};
